import { ref } from 'vue';
import { Toast } from 'vant';
import { getCurrentInstance } from "vue";
export default {
  setup() {
    let {
      proxy
    } = getCurrentInstance();
    const chosenAddressId = ref('1');
    const list = [];

    const fn = () => {
      proxy.$router.back();
    };

    const onAdd = () => {
      proxy.$router.push({
        path: "/address"
      });
    };

    const onEdit = (item, index) => Toast('编辑地址:' + index); // const id=()=>{
    // 	console.log(chosenAddressId.value)
    // 	localStorage.setItem("id",JSON.stringify(chosenAddressId))
    // }


    return {
      // id,
      fn,
      list,
      onAdd,
      onEdit,
      chosenAddressId
    };
  },

  created() {
    if (localStorage.getItem("list")) {
      this.list = JSON.parse(localStorage.getItem("list")); // if(localStorage.getItem("id")){
      // 	console.log(1)
      // 	this.chosenAddressId=JSON.parse(localStorage.getItem("id"))
      // }else{
      // 	console.log(2)
      // 	this.chosenAddressId=ref('1')
      // }
    }
  }

};